import { formatDistanceToNowStrict, parseISO } from "date-fns";

export const formatNumber = (value: number) =>
  new Intl.NumberFormat("en-US", {
    notation: value > 9999 ? "compact" : "standard",
    maximumFractionDigits: 1
  }).format(value);

export const formatPercentage = (value: number, fractionDigits = 1) =>
  `${value.toFixed(fractionDigits)}%`;

export const formatDateTime = (isoDate: string) =>
  formatDistanceToNowStrict(parseISO(isoDate), { addSuffix: true });

export const formatLatency = (value: number) => `${value.toFixed(0)} ms`;

export const formatThroughput = (value: number) => `${value.toFixed(1)} ops/s`;

