import axios from "axios";
import { API_BASE_URL, API_TIMEOUT } from "../utils/constants";
import { getAccessToken } from "./auth";

export const apiClient = axios.create({
  baseURL: API_BASE_URL,
  timeout: API_TIMEOUT
});

apiClient.interceptors.request.use((config) => {
  const token = getAccessToken();
  if (token) {
    config.headers = config.headers ?? {};
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

apiClient.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response?.status === 429) {
      // eslint-disable-next-line no-console
      console.warn("Rate limit encountered. Backing off before retrying.");
    }
    return Promise.reject(error);
  }
);

export const getDashboardMetrics = () => apiClient.get("/api/security/dashboard").then((res) => res.data);

export const getPlayerRiskProfile = (id: number) =>
  apiClient.get(`/api/security/player-risk/${id}`).then((res) => res.data);

export const getSuspiciousPlayers = () =>
  apiClient.get("/api/players/suspicious").then((res) => res.data);

